// Set body background to black
body {
  background-color: #202020;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}

// Position the header at the top of the screen with a height of 60px
header {
  // background-color: #0000006b;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  z-index: 1000; // ensure the header is on top of the slideshow
}

// Position the footer at the bottom of the screen with a height of 40px
footer {
  // position: fixed;
  // bottom: 0;
  // left: 0;
  // right: 0;
  // height: 40px;
  // z-index: 1000; // ensure the footer is on top of the slideshow
  // text-align: right;
  padding: 0.75em 0;
}

.fullscreen-image-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.fullscreen-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.glowing-text.logo-text {
  float: left;
  margin-left: 0.75em;
  margin-top: 0.25em;
  animation: pulse-yellow-purple 2s ease-in-out infinite;
  &:hover {
    animation: glow 2s ease-in-out infinite alternate;
  }
}

// nav {
//   float: right;
// }

h1.title {
  position: fixed;
  bottom: 0em;
  right: 1em;
  font-size: 9em;
  white-space: nowrap;
  display: none;
  // animation: slide-in;
  // animation-duration: 8s;
  // animation-iteration-count: infinite;
}

main {
  margin-top: 7rem;
}

.svg-caption {
  position: absolute;
  left: 15%;
  bottom: 15%;
  background: #000000b5;
  padding: 1em;
  border-radius: 1em;
  border-style: solid;
  border-color: #ffffffa3;
  border-width: 0.1em;
  text-shadow: #000 0.1em 0.1em;

  .caption-title {
    font-weight: 800;
    font-size: 1.5em;
  }
  .caption-description {
    font-style: italic;
  }
}

.target-item {
  opacity: 0;
  transition: opacity 1s;
  &.visible {
    opacity: 1;
  }
}
